		// 提交照片
		submit(){
			if(this.image.some(function(item){return item.name ===""})){
				uni.showToast({
					icon:"none",
				    title: "请上传所需要的所有照片",
				    duration: 2000
				});
			}else{
				let img= []
				this.image.map(item=>{
					img.push(item.name)
				})
				uni.showLoading({
				    title: '上传中'
				});
				uploadimg({
					base:JSON.stringify(img),
					flag:"1",
					id:uni.getStorageSync('id'),
					token:uni.getStorageSync('token'),
				}).then(res=>{
					console.log(res)
					uni.hideLoading();
					if(res.code === "200" && res.data ==="SUCCESS"){
							uni.showToast({
								icon:"none",
							    title: "上传成功",
							    duration: 2000
							});
							uni.setStorageSync('status',10)
							setTimeout(function(){
								uni.switchTab({
									url:"/pages/index/index"
								})
							},2000)
					}else{
						uni.showToast({
							icon:"none",
							title: res.message,
							duration: 2000
						});
					}
				})
			}
		}
	}
